import { Component, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { map } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import { combineLatestWith, Observable, startWith, Subscription } from 'rxjs';
import { ILoginInfo } from '@ApiClient/backend/Login/loginInfo';
import { selectIsLoadingProfile, selectUserLoginInfo, selectUserPosts } from '@store/profile/profile.selectors';
import { getLoginInfoAction } from '@store/profile/profile.actions';
import { AppStateInterface } from '@store/app/app-state.interface';
import { selectCanUpload, selectCurrentUserNickname } from '@store/auth/auth.selectors';
import { IPostFull } from '@ApiClient/backend/Post/post';
import { URLS } from '@enums/urls';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-user-profile',
  templateUrl: 'profile.component.html',
  styleUrls: ['profile.component.scss'],
})
export class ProfileComponent implements OnInit, OnDestroy {
  routeSubscription: Subscription | undefined;
  isCurrentUserProfile: boolean = false;
  locale$: Observable<string> = this.translate.onLangChange.pipe(
    startWith({ lang: this.translate.currentLang }),
    map((event) => event.lang),
  );
  userPosts$: Observable<IPostFull[] | null> = this.store.select(selectUserPosts);
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingProfile);
  canUpload$: Observable<boolean> = this.store.select(selectCanUpload);
  userLoginInfo$: Observable<ILoginInfo | null> = this.store.select(selectUserLoginInfo);

  constructor(
    private store: Store<AppStateInterface>,
    private route: ActivatedRoute,
    private translate: TranslateService,
  ) {}

  ngOnInit() {
    this.routeSubscription = this.route.params
      .pipe(
        combineLatestWith(this.store.select(selectCurrentUserNickname)),
        map(([params, currentUser]) => {
          this.isCurrentUserProfile = params['userSlug'] === currentUser;
          this.store.dispatch(getLoginInfoAction({ nicknameUrl: params['userSlug'] }));
        }),
      )
      .subscribe();
  }

  ngOnDestroy() {
    if (this.routeSubscription) {
      this.routeSubscription.unsubscribe();
    }
  }

  protected readonly URLS = URLS;
}
